import React, { useState } from "react";
import BurgerButton from "../BurgerButton";
import "./HeroBlock.scss";
import MenuBurger from "./MenuBurger/MenuBurger";

const HeroBlock = ({ title, logos, links }) => {
  const [menuActive, setMenuActive] = useState(false);

  const logosImages = logos.map((logoItem, i) => (
    <img
      src={logoItem}
      alt="partner-logo"
      className="hero-partners-item"
      key={i}
    />
  ));
  const navLinks = links.map((link, i) => (
    <a href={link.href} className="hero-navigation-item" key={i}>
      {link.title}
    </a>
  ));
  return (
    <section className="hero">
      <div className="container">
        <div className="hero-partners">{logosImages}</div>
        <div
          className={`hero-burgerBtn ${
            menuActive ? "hero-burgerBtn-desable" : ""
          }`}
          onClick={() => setMenuActive(true)}
        >
          <BurgerButton />
        </div>

        <nav className="hero-navigation">{navLinks}</nav>
        <h1 className="hero-title">{title}</h1>
        <a href="#" className="hero-link">
          Отследить
        </a>
      </div>
      <MenuBurger links={links} active={menuActive} setActive={setMenuActive} />
    </section>
  );
};
export default HeroBlock;
