import React from "react";
import CloseButton from "../../CloseButton";
import "./MenuBurger.scss";

const MenuBurger = ({ links, active, setActive }) => {
  const navLinks = links.map((link, i) => (
    <a href={link.href} className="menu-nav-item" key={i}>
      {link.title}
    </a>
  ));
  return (
    <div className={active ? "menu active" : "menu"}>
      <div className="container">
        <div className="menu-closeBtn" onClick={() => setActive(false)}>
          <CloseButton />
        </div>
        <nav className="menu-nav">{navLinks}</nav>
        <p className="menu-text">2020, Все права защищены</p>
        <p className="menu-text">
          Согласие на обработку персональных данных Политика конфиденциальности
        </p>
      </div>
    </div>
  );
};
export default MenuBurger;
