import React from "react";
import styled from "styled-components";

const Span = styled.span`
  &,
  ::before,
  ::after {
    display: block;
    width: 40px;
    height: 2px;
    background-color: #fff;
  }
  ::before,
  ::after {
    content: "";
  }
  ::before {
    transform: translateY(-7px);
  }
  ::after {
    transform: translateY(7px);
  }
`;

const BurgerButton = () => {
  return <Span />;
};
export default BurgerButton;
