import React from "react";
import styled from "styled-components";

const Span = styled.span`
  ::before,
  ::after {
    display: block;
    width: 40px;
    height: 2px;
    background-color: #fff;
  }
  ::before,
  ::after {
    content: "";
  }
  ::before {
    transform: rotate(-45deg);
  }
  ::after {
    transform: rotate(45deg);
  }
`;

const CloseButton = () => {
  return <Span />;
};
export default CloseButton;
